<template>
<view class="page">
<map id="map" :latitude="lat" :longitude="lng" :markers="markers" scale="16" style="width: 100%; height: 100%;">
</map>
</view>
</template>

<script>

export default {
  data() {
    return {
      lat: '',
      lng: '',
      markers: []
    };
  },

  components: {},
  props: {},

  /**
   * 生命周期函数--监听页面加载
   */
  onLoad: function (options) {
    var marker = {
      id: options.id,
      latitude: options.lat,
      longitude: options.lng,
      width: 50,
      height: 50,
      callout: {
        color: '#F00',
        content: options.address,
        // display:'ALWAYS',
        textAlign: 'center'
      }
    };
    this.setData({
      lat: options.lat,
      lng: options.lng,
      markers: [marker]
    });
  },

  /**
   * 生命周期函数--监听页面初次渲染完成
   */
  onReady: function () {},

  /**
   * 生命周期函数--监听页面显示
   */
  onShow: function () {},

  /**
   * 生命周期函数--监听页面隐藏
   */
  onHide: function () {},

  /**
   * 生命周期函数--监听页面卸载
   */
  onUnload: function () {},

  /**
   * 页面相关事件处理函数--监听用户下拉动作
   */
  onPullDownRefresh: function () {},

  /**
   * 页面上拉触底事件的处理函数
   */
  onReachBottom: function () {},

  /**
   * 用户点击右上角分享
   */
  onShareAppMessage: function () {},
  methods: {}
};
</script>
